.layout{
    display: flex;
    height: 43px;
    //border: 1px solid gray;
    padding: 5px;
    .layout2 {
        width: 90%;
        display: flex;
        .profileLayout{
            height: 42px;
            width: 42px;
            img {
                width: 32px;
            }
        }
        .contentLayout{
            .content{
                display: flex;
                font-size: 14px;
                .userName {
                    font-weight: bold;
                }
                .description{
                    padding-left: 5px;
                }
            }
            .detail {
                display: flex;
                padding: 2px;
                font-size: 12px;
            
                .likes_list{
                    padding: 2px;
                    color: gray;
                }
                .delete {
                    padding: 2px;
                        color: gray;
                }
            }
         }
    }   
    .like_box{
        width: 10%;
    }
}